import React, { useState, useEffect } from "react";
import axios from 'axios';
import './style.css';
// import Facilities from '../components/facilities/FaContents';


function FaContents() {


  const [clintID, setClintID] = useState("8cmdwLcB4sqyQv0NzqxyWvowOQLg7sWZcZakuitoMTg");
  const [Result, setResult] = useState([]);
  const [title, setTitle] = useState();




  useEffect(() => {
    axios.get(`https://api.unsplash.com/photos/?page=1&per_page=6&client_id=${clintID}`)
      .then(res => {
        console.log(res);
        setResult(res.data);
        setTitle(res.data );

      })
  }, [clintID]);



  return (
    <div >
     

<div className=" distance ">



  
  { Result.map((photo) =>
    <div className="row top-buffer ">
       <figure className="figure">
        <img src={photo.urls.small} className="img1" alt="img" />
        <figcaption className="caption">{photo.alt_description} </figcaption>
        </figure>
        </div>

      )}   

   </div>

    </div>
  );
}

export default FaContents;
